import { useEffect, useState } from "react";
import ProductModel from "../models/products";
import "../styles/products.scss";
import { toast } from "react-toastify";

function Sells() {
  const [searchQuery, setQuery] = useState("m");
  const [total, setTotal] = useState(0);
  const [products, setProducts] = useState([]);
  useEffect(() => {
    ProductModel.getSells("m").then((data) => {
      setProducts(data.products);
      setTotal(data.total);
    });
  }, []);
  const listItems = products.map((item, key) => {
    return (
      <tr key={key} className="list-item">
        <td>{item.productName}</td>
        <td>{item.totalPrice}</td>
        <td>{item.totalQuantity}</td>
      </tr>
    );
  });
    const queryChanged = (e) => {
      setQuery(e.target.value);
      ProductModel.getSells(e.target.value)
      .then((data) => {
        setProducts(data.products);
        setTotal(data.total);
      })
      .catch((error) => {
        toast.error("Impossible de charger les produits", {
          autoClose: 5000,
        });
        console.log(error);
      });
    };
  return (
    <div className="container products">
      <div className="action-bar">
        <div className="search-bar">
          <select value={searchQuery} onChange={queryChanged}>
            <option value="m">Mensuel</option>
            <option value="d">Journalier</option>
          </select>
        </div>
        <div className="buttons-bar">
          <button className="btn btn-primary">
            <span>{total}</span>
            <i className="fas fa-dollar"></i>
          </button>
        </div>
      </div>
      <table>
        <thead>
          <tr>
            <th>Produit</th>
            <th>prix</th>
            <th>Quantité</th>
          </tr>
        </thead>
        <tbody>{listItems}</tbody>
      </table>
    </div>
  );
}

export default Sells;
