import React, { useState } from "react";
import ProductModel from "../models/products";
import Papa from "papaparse";
import "../styles/add.scss";
import { toast } from "react-toastify";
import ShowBarcode from "../components/Barcode";

function Add() {
  let productsUp;
  const [products, setProducts] = useState({});
  const [lastProductId, setlastProductId] = useState(0);
  const [barcode, setBarcode] = useState("");
  const [hidden, toogleHidden] = useState(true);
  const handleFileChange = (e) => {
    let file = e.target.files[0];
    if (file) {
      Papa.parse(file, {
        complete: (result) => {
          const toastID = toast.loading("Importation ...");
          console.log(result.data);
          ProductModel.importCSV(JSON.stringify(result.data))
            .then((data) => {
              toast.update(toastID, {
                render: "Les produits ont étés ajoutés",
                type: "success",
                isLoading: false,
                progress: 0,
                autoClose: 6000,
              });
              console.log(data);
            })
            .catch((error) => {
              let msg =
                error.response && error.response.data
                  ? error.response.data.error
                  : error.message;
              toast.update(toastID, {
                render: `Une erreur s'est produite (${msg}) `,
                type: "error",
                isLoading: false,
                progress: 0,
                autoClose: 6000,
              });
              console.log(error);
            });
        },
      });
    }
  };
  const generateBarcode = (key) => {
    productsUp = { ...products };
    let currentCode = productsUp[key][1];
    console.log(currentCode);
    if (currentCode.length > 3) {
      setBarcode(currentCode);
      toogleHidden(false);
    } else {
      productsUp[key][1] = Date.now().toString();
      setBarcode(productsUp[key][1]);
      setProducts(productsUp);
      toogleHidden(false);
    }
  };
  const removeItem = (key) => {
    key = key.toString();
    try {
      productsUp = { ...products };
      delete productsUp[key];
      setProducts(productsUp);
    } catch (error) {
      console.log(error);
    }
  };
  const addItem = () => {
    setlastProductId(lastProductId + 1);
    productsUp = { ...products };
    productsUp[lastProductId] = ["", "", 0, 0];
    setProducts(productsUp);
  };
  //save product
  const saveProducts = () => {
    const toastID = toast.loading("Verification des champs ...");
    productsUp = { ...products };

    for (const [key, value] of Object.entries(productsUp)) {
      console.log(value, productsUp[key]);
      if (value[0].length < 1) {
        delete productsUp[key];
      }
      console.log(value, productsUp[key]);
    }
    console.log(productsUp);
    setProducts(productsUp);
    if (Object.keys(productsUp).length === 0) {
      toast.update(toastID, {
        render: "Ajoutez et remplissez les produits",
        type: "error",
        isLoading: false,
        autoClose: 6000,
      });
      return;
    }
    toast.update(toastID, { render: "Enregistrement ..." });
    ProductModel.create(productsUp)
      .then((response) => {
        setProducts({});
        toast.update(toastID, {
          render: "Les produits ont étés ajoutés",
          type: "success",
          isLoading: false,
          progress: 0,
          autoClose: 6000,
        });
        console.log(response);
      })
      .catch((error) => {
        console.log(error.response.data.error);
        let msg =
          error.response && error.response.data
            ? error.response.data.error
            : error.message;
        toast.update(toastID, {
          render: `Une erreur s'est produite (${msg}) `,
          type: "error",
          isLoading: false,
          progress: 0,
          autoClose: 6000,
        });
        console.log(error);
      });
  };

  const modifyProduct = (id, value, productKey) => {
    productsUp = { ...products };
    if (productKey === 0) value.trim();
    productsUp[id][productKey] = value;
    setProducts(productsUp);
  };
  const listItems = Object.keys(products).map((key) => {
    let item = products[key];
    return (
      <tr key={key} className="list-item">
        <td>
          <input
            type="text"
            value={item[0]}
            onChange={(e) => {
              modifyProduct(key, e.target.value, 0);
            }}
          />
        </td>
        <td>
          <input
            type="text"
            value={item[1]}
            onChange={(e) => {
              modifyProduct(key, e.target.value, 1);
            }}
          />
        </td>
        <td>
          <input
            type="number"
            value={item[2]}
            onChange={(e) => {
              modifyProduct(key, e.target.value, 2);
            }}
          />
        </td>
        <td>
          <input
            type="number"
            value={item[3]}
            onChange={(e) => {
              modifyProduct(key, e.target.value, 3);
            }}
          />
        </td>
        <td className="actions">
          <i
            className="fas fa-barcode"
            onClick={() => generateBarcode(key)}
          ></i>
          <i className="fas fa-close" onClick={() => removeItem(key)}></i>
        </td>
      </tr>
    );
  });
  return (
    <>
      {!hidden && (
        <ShowBarcode hide={() => toogleHidden(true)} barcode={barcode} />
      )}
      <div className="container">
        <div className="card list-card">
          <div className="card-header">
            <span className="card-title">Ajouter des produits</span>
            <div className="buttons">
              <button className="btn btn-primary" onClick={addItem}>
                <i className="fas fa-plus"></i> Ajouter Un produit
              </button>
              <button className="btn btn-success" onClick={saveProducts}>
                <i className="fas fa-save"></i> Enregistrer
              </button>
            </div>
          </div>
          <div className="card-body content">
            <table>
              <thead>
                <tr>
                  <th>Nom du produit</th>
                  <th>Code Barre</th>
                  <th>Prix</th>
                  <th>Quantité</th>
                  <th>Supp.</th>
                </tr>
              </thead>
              <tbody className="add-list">
                {/* Displaying added items */}
                {listItems}
              </tbody>
            </table>
          </div>
        </div>
      </div>
      <div className="container">
        <div className="import-box">
          <h3>
            Vous Pouvez aussi importer un Fichier CSV contenant vos Produits
          </h3>
          <div className="import-group">
            <label htmlFor="csv-input" className="btn btn-primary">
              <i className="fas fa-download"></i> Importer
            </label>
            <input
              type="file"
              name=""
              id="csv-input"
              accept=".csv"
              onChange={handleFileChange}
            />
          </div>
        </div>
      </div>
    </>
  );
}

export default Add;
