import { useEffect, useState } from "react";
import ProductModel from "../models/products";
import "../styles/products.scss";
import { toast } from "react-toastify";
import EditProduct from "../components/EditProduct";
import DialogModal from "../components/DialogModal";

function Products() {
  const [products, setProducts] = useState([]);
  const [searchQuery, setQuery] = useState("");
  const [edition, setEditionState] = useState({ state: false, id: 0 });
  const [deleteModal, toggleDeleteModal] = useState(false);
  const [productToDelete, setProductToDelete] = useState();
  const [critical, setCritical] = useState();
  const hideEdition = () => {
    setEditionState({ state: false, id: 0 });
  };
  useEffect(() => {
    ProductModel.fetch().then((data) => {
      setProducts(data);
    });
    ProductModel.getConfigs().then((data) => {
      setCritical(data.critical);
    });
  }, []);
  useEffect(() => {
    if (searchQuery.length >= 2) {
      ProductModel.search(searchQuery)
        .then((data) => {
          setProducts(data);
        })
        .catch((error) => {
          toast.error("Impossible de charger les produits", {
            autoClose: 5000,
          });
          console.log(error);
        });
    }
  }, [searchQuery]);
  const queryChanged = (e) => {
    setQuery(e.target.value);
  };
  const handleEdition = (id) => {
    setEditionState({ state: true, id });
  };
  const deleteProduct = (id) => {
    const toastID = toast.loading("Suppression en cours ...");
    ProductModel.deleteProduct(id)
      .then((data) => {
        console.log(data);
        toast.update(toastID, {
          render: `Produit effacé !`,
          type: "success",
          isLoading: false,
          progress: 0,
          autoClose: 4000,
        });
      })
      .catch((error) => {
        console.log(error);
        let msg =
          error.response && error.response.data
            ? error.response.data.error
            : error.message;
        toast.update(toastID, {
          render: `Une erreur s'est produite (${msg}) `,
          type: "error",
          isLoading: false,
          progress: 0,
          autoClose: 6000,
        });
      });
  };
  const handleDeleteDialog = (key) => {
    setProductToDelete(products[key]);
    toggleDeleteModal(true);
  };
  const exportProducts = () => {
    const toastID = toast.loading("Construction du CSV ...");
    ProductModel.export()
      .then((file) => {
        /* const file = new Blob([data.csv], { type: 'text/csv' }); */
        const href = URL.createObjectURL(file);
        const link = document.createElement("a");
        link.href = href;
        link.setAttribute("download", "produits-export.csv");
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(href);
        toast.update(toastID, {
          render: "Importation réussi !",
          type: "success",
          isLoading: false,
          progress: 0,
          autoClose: 6000,
        });
      })
      .catch((error) => {
        console.log(error);
        toast.update(toastID, {
          render: `Une erreur s'est produite (${error.message}) `,
          type: "error",
          isLoading: false,
          progress: 0,
          autoClose: 6000,
        });
      });
  };
  const listItems = products.map((item, key) => {
    return (
      <tr key={key} className={"list-item"}>
        <td>{item.productName}</td>
        <td>{item.price}</td>
        <td className={item.quantity<=critical?"critical":""}>{item.quantity}</td>
        <td>
          <div className="actions">
            <i
              className="fas fa-pen"
              onClick={() => handleEdition(item.id)}
            ></i>
            <i
              className="fas fa-close"
              onClick={() => handleDeleteDialog(key)}
            ></i>
          </div>
        </td>
      </tr>
    );
  });
  return (
    <div className="container products">
      {edition.state && <EditProduct id={edition.id} hide={hideEdition} />}
      {deleteModal && (
        <DialogModal hideModal={() => toggleDeleteModal(false)}>
          <div>
            <h3>
              Voulez-vous Vraiment supprimer le produit <br />
              {productToDelete.productName} ?
            </h3>
            <div className="actions-btn">
              <button
                className="btn btn-primary"
                onClick={() => toggleDeleteModal(false)}
              >
                <i className="fas fa-close"></i> Annuler
              </button>
              <button
                className="btn btn-danger"
                onClick={() => {
                  deleteProduct(productToDelete.id);
                  toggleDeleteModal(false);
                }}
              >
                <i className="fas fa-trash"></i> Supprimer
              </button>
            </div>
          </div>
        </DialogModal>
      )}
      <div className="action-bar">
        <div className="search-bar">
          <input
            type="text"
            value={searchQuery}
            onChange={queryChanged}
            name=""
            id=""
            placeholder="Rechercher..."
          />
        </div>
        <div className="buttons-bar">
          <button className="btn btn-success" onClick={exportProducts}>
            <i className="fas fa-download"></i>
          </button>
        </div>
      </div>
      <table>
        <thead>
          <tr>
            <th>Produit</th>
            <th>prix</th>
            <th>Quantité</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody>{listItems}</tbody>
      </table>
    </div>
  );
}

export default Products;
